// Helper function to get visible objects for a specific date
function getVisibleObjectsForDate(dateStr) {
  let date;
  if (dateStr) {
      date = new Date(dateStr);
  } else {
      date = new Date();
  }
  
  // Apply the 3-year modulo wrapping for years beyond 2027
  const year = date.getFullYear();
  if (year > 2027) {
      // Calculate which year in the 2025-2027 cycle to use
      const yearOffset = (year - 2025) % 3;
      const wrappedYear = 2025 + yearOffset;
      
      // Create a new date with the wrapped year but same month/day
      date = new Date(date);
      date.setFullYear(wrappedYear);
  }
  
  const formattedDate = date.toISOString().split('T')[0];
  const dayData = VISIBILITY_CALENDAR[formattedDate];

  if (!dayData) {
      return {
          date: formattedDate,
          north: null,
          south: null
      };
  }

  const northObject = dayData.north ? DSO_DETAILS[dayData.north] : null;
  const southObject = dayData.south ? DSO_DETAILS[dayData.south] : null;
  
  return {
      date: formattedDate,
      north: northObject,
      south: southObject
  };
}


// Function to convert object type code to full readable name
export function getObjectTypeName(typeCode) {
  const typeMap = {
    'G': 'Galaxy',
    'Rad': 'Radio Source',
    'OpC': 'Open Cluster',
    'GlC': 'Globular Cluster',
    'PN': 'Planetary Nebula',
    'HII': 'HII Region',
    'SNR': 'Supernova Remnant',
    'RNe': 'Reflection Nebula',
    'DNe': 'Dark Nebula',
    'LIN': 'LINER-type Active Galaxy',
    'SyG': 'Seyfert Galaxy',
    'X': 'X-ray Source',
    'gam': 'Gamma-ray Source',
    'GiP': 'Galaxy in Pair',
    'ClG': 'Cluster of Galaxies',
    'Sy2': 'Seyfert 2 Galaxy',
    'Cl*': 'Cluster of Stars',
    'rG': 'Radio Galaxy',
    'ISM': 'Interstellar Medium',
    '*': 'Star',
    'PN': 'Planetary Nebula',
    'UV': 'Ultraviolet Source'
  };

  return typeMap[typeCode] || typeCode;
}

